import axios from "axios";
import { message } from "antd";
//  token 
const token = localStorage.getItem("token");

const request = axios.create({
  timeout: 5000,
});

// post
request.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";

// 
request.interceptors.request.use(
  (config) => {
    //  token 
    token && (config.headers.Authorization = token);
    config.headers["PartnerCode"] = "p_bcmotor";
    config.headers["sysProductVersion"] = "V020";
    config.headers["sysVersion"] = "V2";
    config.headers["RequestNo"] = "110";
    config.headers["accountid"] = null;
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// 
request.interceptors.response.use(
  (response) => {
    const res = response.data;
    if (response.status == 200) {
      return res.status == -1
        ? Promise.reject(message.error(res.message))
        : Promise.resolve(res);
    } else {
      return Promise.reject(message.error(res.message));
    }
  },
  (error) => {
    // 
    //  token    
    console.log("error", error);
    switch (error.response.status) {
      case 401:
        break;
      case 403:
        break;
      case 404:
        break;
      case 500:
        break;
      default:
        console.log("");
    }
    return Promise.reject(error);
  }
);

export default request;
