import {
  FileOutlined,
  PieChartOutlined,
  TeamOutlined,
} from "@ant-design/icons";
import { lazy } from "react";
const Home = lazy(() => import(/* webpackChunkName: "Home" */ "../pages/Home"));
const Hooks = lazy(() =>
  import(/* webpackChunkName: "Hooks" */ "../pages/Hooks")
);
const UserManage = lazy(() =>
  import(/* webpackChunkName: "UserManage" */ "../pages/UserManage")
);
const Rudex = lazy(() =>
  import(/* webpackChunkName: "Rudex" */ "../pages/Redux")
);
const UserDetail = lazy(() =>
  import(
    /* webpackChunkName: "UserDetail" */ "../pages/UserManage/compontents/userDetail"
  )
);
const MenuManage = lazy(() =>
  import(/* webpackChunkName: "MenuManage" */ "../pages/MenuManage")
);
const ComponentProps = lazy(() =>
  import(/* webpackChunkName: "ComponentProps" */ "../pages/componentProps")
);
const routes = [
  {
    key: "/home", //menu
    path: "/home", //useRouters
    label: "", //menu()
    element: <Home />, //useRouters
    hidden: 0, //menu,
    icon: <PieChartOutlined />, //menu(icon)
    children: null,
  },
  {
    key: "/react",
    path: "/react",
    label: "react",
    hidden: 0,
    icon: <TeamOutlined />,
    children: [
      {
        key: "/react/react-hooks",
        path: "/react/react-hooks",
        label: "hooks",
        hidden: 0,
        element: <Hooks />,
        children: null,
      },
      {
        key: "/react/redux",
        path: "/react/redux",
        label: "redux",
        hidden: 0,
        element: <Rudex />,
        children: null,
      },
      {
        key: "/react/componentProps",
        path: "/react/componentProps",
        label: "ComponentProps",
        hidden: 0,
        element: <ComponentProps />,
        children: null,
      },
    ],
  },
  {
    key: "/system",
    label: "",
    hidden: 0,
    icon: <FileOutlined />,
    children: [
      {
        key: "/system/menu",
        path: "/system/menu",
        label: "",
        hidden: 0,
        element: <MenuManage />,
        children: null,
      },
      {
        key: "/system/user",
        path: "/system/user",
        label: "",
        element: <UserManage />,
        hidden: 0,
        children: null,
      },
      {
        key: "/system/userDetail",
        path: "/system/userDetail",
        label: "",
        element: <UserDetail />,
        hidden: 1,
        children: null,
      },
    ],
  },
];

//,
const checkRouterPermission = (routers, path) => {
  for (const data of routers) {
    if (data.path == path) return true;
    if (data.children) {
      const res = checkRouterPermission(data.children, path);
      if (res) return true;
    }
  }
  return false;
};

export { routes, checkRouterPermission };
